'use strict';

/**
 * Create App object
 *
 */

var App = {};

(function () {

    App.goto = function (identifier, gotoUp) {
        var current = $('section.visible'),
            next = $('.page.' + identifier),
            direction = gotoUp ? 'up' : 'down';

        if (!next || typeof gotoUp !== 'boolean') {
            throw new Error('Wrong arguments: ' + next + ', ' + gotoUp);
        } else if (current == next) {
            throw new Error('Moving to the same page..');
        } else {
            if (current) {
                var currentName = current.getAttribute('data-page');
                App.call(currentName, 'unloading');
                current.classList.remove('visible');
                current.classList.add('exit-' + direction);
                current.addEventListener('animationend', function animationend(e) {
                    App.call(currentName, 'unloaded');
                    current.removeEventListener('animationend', animationend);
                    current.classList.remove('exit-' + direction);
                });
            }
            App.call(identifier, 'loading');
            next.classList.add('visible');
            next.classList.add('enter-' + direction);
            next.addEventListener('animationend', function animationend(e) {
                App.call(identifier, 'loaded');
                next.removeEventListener('animationend', animationend);
                next.classList.remove('enter-' + direction);
                App.setLocation(identifier);
            });
        }
    };

    App.home = function () {
        // Rerun the page to restart the canvas
        window.location.reload();
    };

    var isChangingHash = false;

    App.setLocation = function (hash) {
        isChangingHash = true;
        window.location.hash = '#/' + hash;
        setTimeout(function () {
            isChangingHash = false;
        }, 1);
    };

    App.gotoHash = function (homeFallback) {
        if (isChangingHash) return;
        if (window.location.hash && window.location.hash.length > 2) {
            var hash = window.location.hash.substring(2),
                page = $('.page.' + hash);

            if (hash === 'home') {
                App.home();
                return;
            } else if (page && page.classList.contains('page')) {
                var current = $('section.visible');
                if (current) {
                    current.classList.remove('visible');
                    var currentName = current.getAttribute('data-page');
                    App.call(currentName, 'unloading');
                    App.call(currentName, 'unloaded');
                }
                page.classList.add('visible');
                App.call(hash, 'loading');
                App.call(hash, 'loaded');
                window.HYPERSPACE = false;
                return;
            }
        }
        if (homeFallback) {
            App.home();
        }
    };

    var callbacks = {};

    App.call = function (page, state) {
        if (callbacks[page] && callbacks[page][state]) {
            for (var i = callbacks[page][state].length - 1; i >= 0; i--) {
                callbacks[page][state][i]();
            }
        }
    };

    App.bind = function (page, state, callback) {
        if (!callbacks[page]) {
            callbacks[page] = {};
        }
        if (!callbacks[page][state]) {
            callbacks[page][state] = [];
        }
        callbacks[page][state].push(callback);
    };
})();
"use strict";

window.AudioContext = (function () {
    return window.AudioContext || window.webkitAudioContext || window.mozAudioContext;
})();
'use strict';

/* Compatible avec tous les navigateurs */
(function () {
    var lastTime = 0;
    var vendors = ['webkit', 'moz'];
    for (var x = 0; x < vendors.length && !window.requestAnimationFrame; ++x) {
        window.requestAnimationFrame = window[vendors[x] + 'RequestAnimationFrame'];
        window.cancelAnimationFrame = window[vendors[x] + 'CancelAnimationFrame'] || window[vendors[x] + 'CancelRequestAnimationFrame'];
    }

    if (!window.requestAnimationFrame) window.requestAnimationFrame = function (callback, element) {
        var currTime = new Date().getTime();
        var timeToCall = Math.max(0, 16 - (currTime - lastTime));
        var id = window.setTimeout(function () {
            callback(currTime + timeToCall);
        }, timeToCall);
        lastTime = currTime + timeToCall;
        return id;
    };

    if (!window.cancelAnimationFrame) window.cancelAnimationFrame = function (id) {
        clearTimeout(id);
    };
})();
"use strict";

var $ = document.querySelector.bind(document),
    $$ = document.querySelectorAll.bind(document);
'use strict';

(function () {

    var episodeIsPlaying = false;

    var canvas = $('.episode-1 canvas.drawing');

    if (!canvas || !canvas.getContext) return;

    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    var context = canvas.getContext('2d'),
        width = canvas.width,
        height = canvas.height;

    // Tutorial from: http://apprentice.craic.com/tutorials/30

    var audioContext;
    var audioBuffer;
    var sourceNode;
    var analyserNode;
    var javascriptNode;
    var audioData = null;
    var audioPlaying = false;
    var sampleSize = 1024;
    var amplitudeArray;
    var audioUrl = 'audio/episode1.mp3';
    var middleWidth = ~ ~(width / 2);
    var middleHeight = ~ ~(height / 2);

    var setupAudioNodes = function setupAudioNodes() {
        sourceNode = audioContext.createBufferSource();
        analyserNode = audioContext.createAnalyser();
        javascriptNode = audioContext.createScriptProcessor(sampleSize, 1, 1);
        // Create the array for the data values
        amplitudeArray = new Uint8Array(analyserNode.frequencyBinCount);
        // Now connect the nodes together
        sourceNode.connect(audioContext.destination);
        sourceNode.connect(analyserNode);
        analyserNode.connect(javascriptNode);
        javascriptNode.connect(audioContext.destination);
    };

    // Load the audio from the URL via Ajax and store it in global variable audioData
    // Note that the audio load is asynchronous
    var loadSound = function loadSound(url) {
        var request = new XMLHttpRequest();
        request.open('GET', url, true);
        request.responseType = 'arraybuffer';
        // When loaded, decode the data and play the sound
        request.onload = function () {
            audioContext.decodeAudioData(request.response, function (buffer) {
                audioData = buffer;
                playSound(audioData);
            }, onError);
        };
        request.send();
    };

    // Play the audio and loop until stopped
    var playSound = function playSound(buffer) {
        sourceNode.buffer = buffer;
        // sourceNode.start();    // Play the sound now
        sourceNode.start(0, 39);
        sourceNode.loop = true;
        audioPlaying = true;
    };

    var onError = function onError(e) {
        console.warn(e);
    };

    var drawSoung = function drawSoung() {
        if (episodeIsPlaying) {
            resetCanvas();
            var length = amplitudeArray.length * 0.95,
                start = ~ ~(length * 0.05);
            context.beginPath();
            context.strokeStyle = 'white';
            context.lineWidth = 4;
            context.moveTo(0, middleHeight);
            // for (var i = length; i >= start; i-=5) {
            for (var i = start; i < length; i += 30) {
                var value = amplitudeArray[i] / 256;
                var y = height - height * value - 1;
                context.lineTo(i * 4 / 4 - start, y);
                // context.fillRect(i-start, y, 1, 30);
            }
            context.stroke();
        }
    };

    var clearCanvas = function clearCanvas() {
        context.clearRect(0, 0, width, height);
    };

    var resetCanvas = function resetCanvas() {
        context.clearRect(0, 0, width, height);
        // context.fillStyle = 'rgba(0, 0, 0, 0.2)';
        // context.fillRect(0, 0, width, height);
    };

    try {
        audioContext = new AudioContext();
    } catch (ex) {
        console.warn('Web Audio API is not supported in this browser');
    }

    App.bind('episode-1', 'loaded', function (e) {
        episodeIsPlaying = true;

        setupAudioNodes();

        // setup the event handler that is triggered every time enough samples have been collected
        // trigger the audio analysis and draw the results
        javascriptNode.addEventListener('audioprocess', function (e) {
            // get the Time Domain data for this sample
            analyserNode.getByteTimeDomainData(amplitudeArray);
            // analyserNode.getByteFrequencyData(amplitudeArray);

            // draw the display if the audio is playing
            if (audioPlaying === true) {
                window.requestAnimationFrame(drawSoung);
            }
        });

        // Load the Audio the first time through, otherwise play it from the buffer
        if (audioData === null) {
            loadSound(audioUrl);
        } else {
            playSound(audioData);
        }
    });

    App.bind('episode-1', 'unloading', function () {
        episodeIsPlaying = false;
        sourceNode.stop(0);
        clearCanvas();
        audioPlaying = false;
    });
})();
'use strict';

(function () {

    var GLOBAL_SPEED = 0.1,
        HYPERSPACE_RUN = true;

    App.bind('home', 'unloading', function () {
        var interval = setInterval(function () {
            GLOBAL_SPEED = (GLOBAL_SPEED + 0.1) * 1.3;
            if (GLOBAL_SPEED > 70) {
                clearInterval(interval);
            }
        }, 100);

        var home = $('.page.home'),
            select = $('.page.select');

        // select.addEventListener('animationend', function begin(e) {
        //     select.removeEventListener('animationend', begin);
        // });

        home.classList.add('exit-hyperspace');
        select.classList.add('enter-hyperspace');
    });

    App.bind('home', 'unloaded', function () {
        HYPERSPACE_RUN = false;

        var home = $('.page.home'),
            select = $('.page.select');

        home.classList.remove('visible');
        home.classList.remove('exit-hyperspace');
        select.classList.remove('enter-hyperspace');
    });

    (function (canvas) {

        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;

        if (canvas.getContext) {
            var context = canvas.getContext('2d'),
                width = canvas.width,
                height = canvas.height,
                starsCount = 100,
                centerRadius = 100,
                length = 10,
                stars = [];

            var createStar = function createStar() {
                var randomX = Math.random(),
                    randomY = Math.random(),
                    color = ~ ~(Math.random() * 80 + 20);

                return {
                    x: randomX * width,
                    y: randomY * height,
                    xs: (randomX * width - width / 2) / 500,
                    ys: (randomY * height - height / 2) / 500,
                    size: ~ ~(color / 20),
                    length: Math.random() * length,
                    color: 'rgb(' + color + ', ' + color + ', ' + ~ ~(color + Math.random() * 20) + ')'
                };
            };

            for (var i = 0; i < starsCount; i++) {
                stars.push(createStar());
            }

            (function loop() {
                if (HYPERSPACE_RUN) {

                    window.requestAnimationFrame(loop);
                    context.clearRect(0, 0, width, height);

                    for (var i = 0; i < starsCount; i++) {
                        var s = stars[i];
                        if (s.x < 0 || s.y < 0 || s.x > width || s.y > height) {
                            s = stars[i] = createStar();
                        }
                        context.strokeStyle = s.color;
                        context.lineWidth = s.size;
                        context.shadowBlur = 5;
                        context.lineCap = 'round';
                        context.shadowColor = "#555";
                        context.beginPath();
                        context.moveTo(s.x, s.y);
                        context.lineTo(s.x + 0.1 + s.length * s.xs * GLOBAL_SPEED, s.y + s.length * s.ys * GLOBAL_SPEED);
                        context.stroke();
                        s.x += s.xs * GLOBAL_SPEED;
                        s.y += s.ys * GLOBAL_SPEED;
                    }
                }
            })();
        }
    })($('.hyperspace'));
})();
'use strict';

/**
 * Navigation between pages
 *
 */

// Bind begin button to hyperspace animation
$('.begin').addEventListener('click', function (e) {
    App.call('home', 'unloading');
    App.call('select', 'loading');
    var select = $('.page.select');
    select.addEventListener('animationend', function begin(e) {
        select.removeEventListener('animationend', begin);
        App.call('home', 'unloaded');
        App.call('select', 'loaded');
        $('.page.select').classList.add('visible');
        App.setLocation('select');
    });
});

// Bind navigation elements to animations
var bindNavigation = function bindNavigation(element) {
    element.addEventListener('click', function (e) {
        App.goto(this.getAttribute('data-target'), this.getAttribute('data-direction') === 'up');
        e.preventDefault();
    });
};

var elements = $$('.navigation');
for (var i = elements.length - 1; i >= 0; i--) {
    bindNavigation(elements[i]);
}

// Change page if hash change
window.addEventListener('hashchange', App.gotoHash);

// If a hash is already set
App.gotoHash(false);
'use strict';

(function () {

    var select = {
        lastMove: 0,
        leftOffset: window.screen.width / 2 - 100,
        rightOffset: 0,
        screenScale: 100 - 100 / 1.75,
        selectCards: $('.select .cards')
    };

    select.percentDivisor = window.screen.width - select.rightOffset - select.leftOffset;

    var toggleSelectionListener = function toggleSelectionListener(event) {
        var now = new Date().getTime();
        if (select.lastMove < now - 20) {

            select.lastMove = now;
            var percent = (event.screenX - select.leftOffset) / select.percentDivisor;

            if (percent < 0) {
                percent = 0;
            } else if (percent > 1) {
                percent = 1;
            }

            select.selectCards.style.transform = 'translateZ(0) translateX(-' + percent * select.screenScale + '%)';
        }
    };

    App.bind('select', 'loading', function () {
        document.addEventListener('mousemove', toggleSelectionListener);
    });

    App.bind('select', 'unloading', function () {
        document.removeEventListener('mousemove', toggleSelectionListener);
    });
})();
'use strict';

